import React, { useEffect, useState } from 'react'
import UserInfo from './UserInfo'
import { userService } from '../../_services/user.service'
import { VerticalTimeline, VerticalTimelineElement } from '../Shared/timeline';
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";
import LoplobItem from './LoplobItem';


const UserLoplobs = props => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState([])
  const [userInfo, setUserInfo] = useState(props.data)

  useEffect(() => {

    getData()

    setUserInfo(props.data)

  }, [props.id, props.userInfo])

  const getData = async () => {

    setLoading(true)

    await userService.userLoplobs(props.id)

      .then((res) => {

        console.log(res.data.purchased_loplobs);

        setData(res.data.purchased_loplobs)

        setLoading(false)

      })
      .catch((error) => {

        console.log(error);

        setLoading(false)

      });

  }

  return (
    data &&
    <div>

      {/* <UserInfo data={userInfo} /> */}

      <div style={{ direction: 'ltr' }}>
        {data && data.length > 0 ?
          <VerticalTimeline>
            {data && data.map((item, index) => (
              <React.Fragment key={item.id}>
                <LoplobItem data={item} />
              </React.Fragment>
            ))}
          </VerticalTimeline>
          :
          !loading ?
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px', display: (!data) || loading ? 'none' : 'block' }}>رکوردی یافت نشد</div>
            :
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px' }}><AiOutlineLoading className="mdi-spin" size="22px" /></div>
        }
      </div>

    </div>

  )
}
export default UserLoplobs;