import React, { Component, useState, useEffect } from 'react'
import { Tabs, Tab } from 'react-bootstrap'
import UserInfo from './UserInfo'
import UserHistoryTimeline from './UserHistoryTimeline'
import UserLoplobs from './UserLoplobs'
import UserTreasures from './UserTreasures'
import UserTransactions from './UserTransactions'
import { usersService } from '../../_services/users.service'


const UserModal = props => {
  const [activeTab, setActiveTab] = useState('userHistory')
  const [loading, setLoading] = useState(false)
  const [id, setId] = useState(props.id)
  const [data, setData] = useState({})

  useEffect(() => {

    getData()

  }, [props.id])

  const getData = async () => {

    setLoading(true)

    await usersService.getById(id)

      .then((res) => {

        console.log(res.data);

        setData(res.data ? res.data.user : {})

        setLoading(false)

      })
      .catch((error) => {

        console.log(error);

        setLoading(false)

      });

  }

  return (

    <>

      <UserInfo data={data} />

      <Tabs transition={false}
        id="controlled-tab-example"
        activeKey={activeTab}
        onSelect={activeTab => setActiveTab(activeTab)}
      >
        {/* <Tab eventKey="userInfo" title="مشخصات کاربر">
        {activeTab === 'userInfo' ?
          <UserInfo data={data} />
          : ''}
      </Tab> */}
        <Tab eventKey="userHistory" title="سوابق خرید کاربر">
          {activeTab === 'userHistory' ?
            <UserHistoryTimeline data={data} />
            : ''}
        </Tab>
        <Tab eventKey="userLoplobs" title="لُپ لُب">
          {activeTab === 'userLoplobs' ?
            <UserLoplobs data={data} id={data.id} />
            : ''}
        </Tab>
        <Tab eventKey="userTransactions" title="گنج ها">
          {activeTab === 'userTransactions' ?
            <UserTreasures data={data} id={data.id} />
            : ''}
        </Tab>
        <Tab eventKey="userTreasures" title="گزارشات مالی">
          {activeTab === 'userTreasures' ?
            <UserTransactions data={data} id={data.id} />
            : ''}
        </Tab>
      </Tabs>

    </>
  )
}
export default UserModal;