import React, { useEffect, useState } from 'react'


const UserInfo = props => {
  const [data, setData] = useState(props.data)

  useEffect(() => {
    setData(props.data)
  }, [props.data])

  return (
    data &&

    <fieldset className="fieldset">
      <legend>مشخصات کاربر</legend>
      <table className="table validator-table" style={{ marginBottom: 5 }}>
        <thead>
          <tr>
            <th className="text-center">استان</th>
            <th className="text-center">شهرستان</th>
            <th className="text-center">نام</th>
            <th className="text-center">نام خانوادگی</th>
            <th className="text-center">موبایل</th>
            <th className="text-center">تاریخ تولد</th>
            <th className="text-center">میزان اعتبار</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{data.state}</td>
            <td>{data.city}</td>
            <td>{data.first_name}</td>
            <td>{data.last_name}</td>
            <td>{data.mobile}</td>
            <td>{data.dob}</td>
            <td>{data.credit}</td>
          </tr>
        </tbody>
      </table>
    </fieldset>

  )
}
export default UserInfo;