import React, { useState, useEffect } from 'react'
import { VerticalTimeline, VerticalTimelineElement } from '../Shared/timeline';
// import Loading2 from '../../Shared/Loading2'
// import NumberFormat from 'react-number-format'
import config from '../../config'
import { MdShoppingCart, MdDateRange } from 'react-icons/md'
import { GiCash } from 'react-icons/gi'


const LoplobItem = (props) => {
  const [data, setData] = useState(props.data);

  useEffect(() => {
    setData(props.data)
    console.log(props.data);

  }, [props.data])
  return (

    <React.Fragment>
      <VerticalTimelineElement className={data.value ? "timeline-data-container has-gift" : "timeline-data-container"}>

        <div className={data.value ? "timeline-data  " : "timeline-data "}>
          <div className="timeline-infos">
            <div className="row">
              <div className="col-sm-12 col-xs-12">
                <div className="timeline-item">
                  <span className="value">
                    {data.date}
                  </span>
                </div>
              </div>
              <div className="col-sm-12 col-xs-12">
                <div className="timeline-item" style={{ border: 'none' }}>
                  {/* <span className="key">تاریخ اسکن:</span> */}
                  <span className="value">
                    {
                      data.value
                        ? <> {'( ' + data.value + ' ریال ) '} <GiCash size="24px" color="#ffff00" /> </>
                        : 'پوچ'
                    }

                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

      </VerticalTimelineElement>
    </React.Fragment>

  )
}

export default LoplobItem;
