import axios from 'axios'
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import config from '../config';
const base_url = config.apiUrl;

const model = 'loplobs/';

export const loplobsService = {
  getAll,
  getById,
  add,
  edit,
  delete: _delete,

  getAllValue,
  addValue,
  editValue,
  deleteValue,
};


function getAll() {
  const url = base_url + model;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function getById(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function add(dto) {
  const url = base_url + model;
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function edit(id, dto) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.put(url, dto, { headers: authHeaderJson() });
}

function _delete(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.delete(url, { headers: authHeader() });
}



function getAllValue(id) {
  const url = base_url + model + id + '/loplob_values';
  console.log(url);
  
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader() });
}

function addValue(id, dto) {
  const url = base_url + model + id + '/loplob_values';
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson() });
}

function editValue(id, dto) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.put(url, dto, { headers: authHeaderJson() });
}

function deleteValue(id, value_id) {
  const url = base_url + model + id + '/loplob_values/' + value_id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.delete(url, { headers: authHeader() });
}