import React, { Component } from 'react'
import QRCode from 'qrcode.react'
// import AssignGift from './AssignGift'
import { MdCardMembership, MdClose } from 'react-icons/md'
import { loplobsService } from '../../_services/loplobs.service'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../Shared/MySweetAlert'
import { FaPlus } from 'react-icons/fa'
import { FiEdit, FiGift } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai"
import Loading from '../Shared/Loading'

import { Formik, Field, getIn, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../Shared/ErrorPopUp'

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Slide from '@material-ui/core/Slide';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from '@material-ui/core/Fab'
import PriceInput from '../Shared/PriceInput'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

class LoplobValuesList extends Component {

  constructor(props, context) {
    super(props, context);

    var pageSize = parseInt(localStorage.getItem('pageSizeForProducts'), 0)
    this.state = {
      loplobId: this.props.loplobId,
      loading: true,
      model: {},
      showAssignGift: false,
      list: [],
      confirmDeleteShow: false,
    }

    this.getData()

  }


  componentWillReceiveProps(nextProps) {

    if (nextProps.loplobId !== this.state.loplobId) {

      this.setState({ loplobId: nextProps.loplobId }, () => {
        this.getData()
      });

    }

  }

  getData() {
    loplobsService.getAllValue(this.state.loplobId)
      .then((res) => {
        console.log(res.data)
        this.setState({ list: res.data.loplob_values, loading: false, isSubmitting: false, modal: false });
      })
      .catch((error) => {
        // this.handleError(error)
      });
  };

  handleModalCreate = () => {
    this.setState({ modalHeading: 'ثبت جایزه جدید', modal: true, model: { value: '', qty: '' } });
  }

  handleModalHide = (e) => {
    var evt = e || window.event; // IE compatibility
    if (evt.preventDefault) {
      evt.preventDefault();
    }
    this.setState({ modal: false });
  }

  handleSubmit = (values, setSubmitting) => {
    this.setState({ loading: true });
    // console.log(JSON.stringify(values));
    values["loplob_id"] = this.state.loplobId;

    console.log(JSON.stringify(values));
    loplobsService.addValue(this.state.loplobId, JSON.stringify(values))
      .then((res) => {

        toastr.success('ثبت جایزه جدید', 'جایزه "' + res.data.name + '" با موفقیت ثبت شد');
        this.setState({ modal: false });
        this.getData();
        setSubmitting(false);

      })
      .catch((error) => {
        this.handleError(error)
        setSubmitting(false);
      });
  }


  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleAssignModalClose = () => {

    this.setState({ model: {}, showAssignGift: false })

  }

  handleCloseAndReload = (val) => {

    let tmp = this.state.list;

    tmp.list[val.index] = val;

    this.setState({ model: {}, showAssignGift: false, data: tmp })

  }

  handleDelete = (e) => {
    let _model = this.state.list[e];

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });

    loplobsService.deleteValue(this.state.loplobId, this.state.model.id)
      .then((res) => {
        toastr.success('حذف جایزه', 'جایزه با موفقیت حذف شد');
        this.getData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  render() {
    const { loading, list, showAssignGift, model } = this.state;

    const ValidationSchema = Yup.object().shape({
      value: Yup.string().trim().required('مبلغ جایزه'),
      qty: Yup.string().trim().required('تعداد'),
    });

    return (
      <div>
        <a onClick={this.handleModalCreate} className="btn btn-primary react-table-add-btn pull-left" >
          <span>
            <FaPlus size="16px" />
        افزودن جایزه جدید
      </span>
        </a>
        <br />
        <hr />
        <table className="table validator-table" style={{ marginBottom: 5 }}>
          <thead>
            <tr>
              <th className="text-center" style={{ width: 150 }}>ردیف</th>
              <th className="text-center">تعداد</th>
              <th className="text-center">مبلغ جایزه</th>
              <th className="text-center" style={{ width: 150 }}>حذف</th>
            </tr>
          </thead>
          <tbody>
            {list && list.map((item, index) => (
              <tr key={"p-" + index} >
                <td>{index + 1}</td>
                <td>
                  {item.qty}
                </td>
                <td>
                  {item.value}
                </td>
                <td>
                  <a onClick={() => this.handleDelete(index)} data-index={index} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        <Dialog
          open={this.state.modal}
          onClose={this.handleModalHide}
          TransitionComponent={Transition}
          scroll={'paper'}
          fullWidth={true}
          maxWidth="xs"
          aria-labelledby="scroll-dialog-title"
          aria-describedby="scroll-dialog-description">
          <DialogTitle>
            <Fab color="secondary" aria-label="refresh" size="small" onClick={() => this.handleModalHide()} className="modal-close-btn">
              <MdClose size="20px" />
            </Fab>
            <span className="dialogTitle">{this.state.dialogTitle}</span>
            <span style={{ marginRight: 10 }}></span>
          </DialogTitle>
          <DialogContent dividers={true}
            style={{ overflowY: 'visible', minHeight: 225 }}
          >

            <Formik
              initialValues={model}
              onSubmit={(values, { setSubmitting }) => {
                // // console.log(values);
                this.handleSubmit(values, setSubmitting)
              }}
              validationSchema={ValidationSchema}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  setFieldValue,
                  handleSubmit,
                } = props;
                return (

                  <div className="row" style={{ position: "relative" }}>
                    <div className="col-md-12">
                      <form onSubmit={handleSubmit}>

                        <div className="row">
                          <div className="form-inline">

                            <div className="col-md-12 col-sm-12">
                              <label>مبلغ جایزه</label>
                              <PriceInput
                                defaultValue={values.value}
                                onChange={val => {
                                  setFieldValue("value", val)
                                }}
                                className={
                                  getIn(errors, "value") &&
                                    getIn(touched, "value")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="value" />
                            </div>

                            <div className="col-md-12 col-sm-12">
                              <label>تعداد</label>
                              <Field type="text"
                                autoFocus
                                name="qty"
                                className={
                                  getIn(errors, "qty") &&
                                    getIn(touched, "qty")
                                    ? "text-input has-error ltr"
                                    : "text-input required ltr"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="qty" />
                            </div>
                            <div className="clearfix form-row"></div>
                            {/* 
                    <div className="col-md-12">
                      <label className="pull-right">تصویر</label>
                      <PicUploader label="تصویر"
                        value={'wwwroot/' + values.image}
                        defaultValue={'wwwroot/' + values.image}
                        onChange={(val) => {
                          setFieldValue('image', val);
                        }}
                      />
                      <ErrorMessage component={ErrorPopUp} name="image" />
                    </div>
                    <div className="clearfix form-row"></div> */}

                            <div className="col-md-12">
                              <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}>
                                {isSubmitting ?
                                  <AiOutlineLoading className="mdi-spin" size="22px" />
                                  :
                                  <AiOutlineSave size="22px" />
                                }ذخیره</button>
                            </div>
                            <div className="clearfix form-row"></div>

                          </div>
                        </div>


                      </form>
                    </div>
                  </div>

                );
              }}
            </Formik>

          </DialogContent>
        </Dialog >
        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف این مورد مطمئن هستید؟"}
        >
        </MySweetAlert>
        {loading && <Loading />}
        {/* <AssignGift data={model} open={showAssignGift} onClose={() => this.handleAssignModalClose()} onCloseAndReload={(val) => this.handleCloseAndReload(val)} /> */}
      </div >
    )
  }
}

export default LoplobValuesList;