import React, { useEffect, useState } from 'react'
import UserInfo from './UserInfo'
import { userService } from '../../_services/user.service'
import { VerticalTimeline, VerticalTimelineElement } from '../Shared/timeline';
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";


const UserTransactions = props => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState([])
  const [userInfo, setUserInfo] = useState(props.data)

  useEffect(() => {

    getData()

    setUserInfo(props.data)

  }, [props.id, props.userInfo])

  const getData = async () => {

    setLoading(true)

    await userService.userTransactions(props.id)

      .then((res) => {

        console.log(res.data);

        setData(res.data.user_transactions)

        setLoading(false)

      })
      .catch((error) => {

        console.log(error);

        setLoading(false)

      });

  }

  return (
    data &&
    <div>

      {/* <UserInfo data={userInfo} /> */}
      {/* <hr /> */}

      <div style={{ direction: 'ltr' }}>
        {data && data.length > 0 ?
          <table className="table validator-table rtl" style={{ marginBottom: 5 }}>
            <thead>
              <tr>
                <th className="text-center">ردیف</th>
                <th className="text-center">نوع عملیات</th>
                <th className="text-center">مبلغ</th>
                <th className="text-center">تاریخ</th>
              </tr>
            </thead>
            <tbody>
              {data && data.map((item, index) => (
                <tr>
                  <td>{index + 1}</td>
                  <td>
                    {
                      item.type === 0
                        ? 'شارژ'
                        : item.type === 1
                          ? 'شارژ توسط ادمین'
                          : item.type === 2
                            ? 'اسکن محصول'
                            : item.type === 3
                              ? 'اسکن گنج'
                              : item.type === 4
                                ? 'خرید لپ لب'
                                : ''
                    }
                  </td>
                  <td>
                    <div className="price" style={{ justifyContent: 'center' }}>
                      {item.amount}
                      <span className="rial"> ریال</span>
                    </div>
                  </td>
                  <td>{item.date}</td>
                </tr>
              ))}
            </tbody>
          </table>
          :
          !loading ?
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px', display: (!data) || loading ? 'none' : 'block' }}>رکوردی یافت نشد</div>
            :
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px' }}><AiOutlineLoading className="mdi-spin" size="22px" /></div>
        }
      </div>

    </div>

  )
}
export default UserTransactions;