import React, { Component, useState, useEffect } from 'react'
import UserInfo from './UserInfo'
import { VerticalTimeline, VerticalTimelineElement } from '../Shared/timeline';
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai";
import GiftItem from './GiftItem';


const UserHistoryTimeline = props => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(props.data)

  useEffect(() => {
    setData(props.data)
  }, [props.data])

  return (
    <>

      {/* <UserInfo data={data} /> */}

      <div style={{ direction: 'ltr' }}>
        {data && data.history && data.history.item_codes.length > 0 ?
          <VerticalTimeline>
            {data && data.history && data.history.item_codes.map((item, index) => (
              <React.Fragment key={item.id}>
                <GiftItem data={item} />
              </React.Fragment>
            ))}
          </VerticalTimeline>
          :
          !loading ?
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px', display: (!data) || loading ? 'none' : 'block' }}>رکوردی یافت نشد</div>
            :
            <div style={{ textAlign: 'center', width: '100%', color: '#F34255', marginTop: 30, fontWeight: 'bold', fontSize: '15px' }}><AiOutlineLoading className="mdi-spin" size="22px" /></div>
        }
      </div>

    </>
  )
}
export default UserHistoryTimeline;